.checkbox-content {
  margin-left: -16px;
  padding-left: 16px;
  padding-top: 2px;
  height: 300px;

  @include _1150 {
    height: 100%;
  }

  .checkbox {
    width: 18px;
    height: 18px;
    border: none;
    position: absolute;
    opacity: 0;
  }

  &__label {
    display: block;
    width: calc(100% - 14px);
    height: 31px;
    position: relative;

    &::before {
      @include ghost;
      left: -16px;
      width: calc(100% + 16px);
      cursor: pointer;
      background-color: transparent;
      transition: background-color .3s ease-in-out;
    }

    &:hover.checkbox-content__label::before {
      background-color: var(--color-pretty-grey);
      transition: background-color .3s ease-in-out;
    }

  }

  .label__text {
    position: relative;
    width: 100%;
    height: 31px;
    display: flex;
    align-items: center;
    padding-left: 33px;
    cursor: pointer;
  }

  .label__text::before {
    content: "";
    position: absolute;
    left: 0;
    // top: 0;
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: var(--border-radius);
    transition: border-color .3s ease-in-out;
    border: 3px solid var(--color-silver-chalice);
  }

  .label__text::after {
    content: "";
    position: absolute;
    left: 0;
    // top: 0;
    width: 18px;
    height: 18px;
    background-image: url("../img/checkboxes.svg");
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0;
  }

  .checkbox:checked+.label__text::after {
    opacity: 1;
  }

  .checkbox:focus+.checkbox-content__label::before {
    transition: background-color .3s ease-in-out;
    background-color: var(--color-pretty-grey);
  }

  // .checkbox:hover+.label__text {
  //   background-color: var(--color-pretty-grey);
  // }

  .checkbox:active+.label__text {
    color: var(--dark-purple-color)
  }
}
