.catalog-hero {
  padding-bottom: 24px;

  &__container {
    padding: 12px 14px 0;
    display: block;
    position: relative;

    @include _480 {
      padding: 12px 8px 0;
    }
  }

  &__heading {
    margin: 0;
    margin-bottom: 48px;
  }

  &-status {
    padding: 26px 16px;
    margin-bottom: 24px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: var(--border-radius);
    background-color: var(--color-white);

    @include _1150 {
      background-color: transparent;
      padding: 0;
      box-shadow: none;
    }

    &__wrapper {
      @include _1150 {
        display: none;
      }
    }

    &__block {

      &:not(:last-child) {
        margin-bottom: 12px;
        border-bottom: 1px solid var(--color-mercury);
      }

      &:last-child(2) {
        margin-bottom: 24px;
      }

      &--boiler {
        display: none;
      }

      &--parts {
        display: none;
      }

      &--reset {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }

    &__title {
      margin: 0;
      margin-bottom: 16px;
      font-weight: 500;
      font-size: 14px;
      color: var(--color-boulder);
    }

    &__inner {
      display: flex;
      align-items: center;
    }

    &__item {
      display: none;
      cursor: pointer;
      margin-bottom: 10px;
      margin-right: 12px;
      font-size: 14px;
      font-weight: 500;
      padding: 4px;
      padding-left: 16px;
      border-radius: 40px;
      border: 1px solid var(--color-alto);
      transition: background-color .3s ease-in-out;
      color: var(--color-tundora);

      &:hover svg {
        fill: var(--color-mandy);
        transition: fill .3s ease-in-out;
      }

      &:hover {
        background-color: var(--color-hawkes-secon-blue);
        transition: background-color .3s ease-in-out;
      }
    }

    .catalog-hero-status__item--active {
      display: inline-block;
    }

    &__name {
      display: block;
      margin-right: 8px;
    }

    &__close {
      display: block;
      position: relative;
      width: 32px;
      height: 32px;
      border-radius: 100%;
      background-color: var(--color-white);

      svg {
        width: 10px;
        height: 10px;
        fill: var(--color-matisse);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: fill .3s ease-in-out;
      }
    }

    &__reset-btn {
      padding: 11px 16px;
      font-size: 14px;
      font-weight: 500;
      border: 1px solid var(--color-mandy);
      border-radius: 40px;
      color: var(--color-tundora);
      transition: background-color .3s ease-in-out, color .3s ease-in-out;

      @include _1150 {
        display: none;
      }

      &:disabled {
        cursor: auto;
        color: var(--color-nobel);
        border-color: var(--color-mercury);
      }

      &--hover {
        &:hover {
        background-color: var(--color-mandy);
        color: var(--color-athens-gray);
        transition: background-color .3s ease-in-out, color .3s ease-in-out;
       }

       @include _1150 {
        background-color: var(--color-mandy);
        color: var(--color-athens-gray);
      }

      &:active {
        background-color: var(--color-well-read);
      }
      }

      &--mobile {
        display: none;

        @include _1150 {
          display: block;
          padding: 7px 8px;
        }
      }
    }

    &__filter-btn {
      padding: 8px;
      width: 200px;
      display: none;
      background-image: url("../img/filter-icon.svg");
      background-position: left 40px center;
      background-repeat: no-repeat;
      border-radius: var(--border-radius);
      border: 1px solid var(--color-matisse);
      font-size: 14px;
      background-color: var(--color-matisse);
      color: var(--color-athens-gray);

      @include _1150 {
        display: block;
        padding: 11px 8px;
      }

      @include _480 {
        width: 50%;
        margin-right: 8px;
        background-position: left 20px center;
      }
    }
  }

  &-content {
    display: grid;
    gap: 16px;
    grid-template-columns: 24% 2fr;

    @include _1215 {
      grid-template-columns: 27% 2fr;
    }

    @include _1150 {
      grid-template-columns: 1fr;
    }

    &__wrapper {
      padding-bottom: 12px;
      margin-bottom: 26px;
      border-bottom: 1px solid var(--color-mercury);

      @include _1150 {
        margin-bottom: 12px;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }

    &__title {
      margin: 0;
      font-size: 24px;
      font-weight: 400;
      color: var(--color-pretty-black);

      @include _1150 {
        font-size: 22px;
      }
    }

    &__title--mobile {
      padding-left: 32px;
    }

    &__inner {
      transition: background .3s ease-in-out,
      opacity .3s ease-in-out,
      visibility .3s ease-in-out;

      @include _1150 {
        position: absolute;
        width: 100%;
        z-index: 300;
        top: -200px;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0;
        visibility: hidden;
        height: 100%;
      }
    }

    &__right {
      padding: 24px 16px;
      height: fit-content;
      border-radius: var(--border-radius);
      box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
      background-color: var(--color-white);
      transition: left .3s ease-in-out,
      opacity .3s ease-in-out,
      visibility .3s ease-in-out;

      @include _1150 {
        position: fixed;
        overflow-y: scroll;
        width: 320px;
        top: 0;
        left: -100vw;
        opacity: 0;
        visibility: hidden;
        z-index: 200;
        min-height: 410px;
        height: 100%;
      }
    }

    &__inner.mobile-filter-active {
      opacity: 1;
      // z-index: 300;
      visibility: visible;
      background: rgba(24, 24, 24, 0.61);
      transition: background .3s ease-in-out,
      opacity .3s ease-in-out,
      visibility .3s ease-in-out;
    }

    &__right.mobile-filter-active {
      left: 0;
      opacity: 1;
      visibility: visible;
      transition: left .3s ease-in-out,
      opacity .3s ease-in-out,
      visibility .3s ease-in-out;
    }

    &__accordion {
      margin-bottom: 53px;
    }

    &__btns {
      display: flex;
      flex-direction: column;
      align-items: center;

      @include _1150 {
        position: absolute;
        bottom: 0;
        left: 16px;
        right: 16px;
      }
    }

    &__btn {
      padding: 8px 32px;
      width: 100%;
      border-radius: var(--border-radius);
      border: 1px solid var(--color-matisse);
      font-size: 18px;
      transition: border-color .3s ease-in-out,
      color .3s ease-in-out,
      background-color .3s ease-in-out;

      &--show {
        margin-bottom: 24px;
        background-color: var(--color-matisse);
        color: var(--color-athens-gray);

        &:hover {
          border-color: var(--color-mariner);
          background-color: var(--color-mariner);
          transition: background-color .3s ease-in-out;
        }

        &:active {
          border-color: var(--color-blumine);
          background-color: var(--color-blumine);
        }
      }

      &--reset {
        color: var(--color-matisse);
        background-color: var(--color-hawkes-blue);

        &:hover {
          border-color: var(--color-mariner);
          color: var(--color-mariner);
          background-color: transparent;
          transition: color .3s ease-in-out, background-color .3s ease-in-out;
        }

        &:active {
          border-color: var(--color-blumine);
          color: var(--color-blumine);
        }

        @include _1150 {
          display: none;
        }
      }
    }

    &__load-more {
      display: flex;
      align-items: center;
      justify-content: center;

      .load-more__load {
        display: block;
        width: 48px;
        height: 48px;
        margin-right: 24px;
        background-image: url("../img/rotate-cw.svg");
        background-repeat: no-repeat;
        background-position: left center;
        animation-duration: 1s;
        animation-iteration-count:infinite;
        animation-timing-function:linear;


        @keyframes ball_moveG{
          0%{
            transform:rotate(0deg)
          }

          100%{
            transform:rotate(360deg)
          }
        }
      }

      .load-more__btn {
        font-size: 24px;
        color: var(--color-matisse);
      }

    }
  }
}

.product-list {
  display: grid;
  margin-bottom: 48px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 16px;
  row-gap: 26px;

  @include _1215 {
    grid-template-columns: 1fr 1fr 1fr;
  }

  @include _1150 {
    column-gap: 10px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  @include _850 {
    grid-template-columns: 1fr 1fr 1fr;
  }

  @include _530 {
    grid-template-columns: 1fr 1fr;
    column-gap: 8px;
  }

  &__article {
    @include _1215 {
      width: auto !important;
    }
  }

  &__item {
    height: fit-content;
    transition: box-shadow .3s ease-in-out;

    &:hover .product-list__add {
      opacity: 1;
      visibility: visible;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    }

    &:hover {
      transition: box-shadow .3s ease-in-out;
      box-shadow: -1px 4px 30px rgba(0, 0, 0, 0.16);
    }

  }

  &__title {
    margin: 0;
    font-weight: 400;
  }

  &__status {
    position: absolute;
    bottom: 24px;
    right: 16px;
    display: flex;
    height: 40px;
    width: 40px;
    border-radius: var(--border-radius);

    & span {
      height: 100%;
      width: 100px;
      background-repeat: no-repeat;
      background-position: center;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    }
  }

  &__add {
    opacity: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-golden-tainoi);
    border-radius: var(--border-radius);
    visibility: hidden;
    display: inline-block;
    transition: background-color .3s ease-in-out;
    background-image: url("../img/cart.svg");

    @include _1150 {
      opacity: 1;
      visibility: visible;
    }

    &:hover {
      transition: background-color .3s ease-in-out;
      background-color: var(--color-bright-sun);
    }
  }

  &__added {
    background-image: url("../img/added-cart.svg");
    display: none;
  }
}

.pointer-events-none {
  pointer-events: none;
}

.accordion {

  &__title {
    margin: 0;
    cursor: pointer;
    position: relative;
    margin-bottom: 15px;
    font-size: 18px;
    font-weight: 500;

    &:not(:last-child) {
      @include _1150 {
        padding-bottom: 12px;
    }
    }
  }

  &__title--mobile {
    margin: 0;
    font-size: 22px;
    padding-bottom: 0 !important;
    padding-left: 32px;
    line-height: 32px;
    max-width: 165px;
  }

  &__title--1 {
    @include _1150 {
      margin-bottom: 57px;
      border-bottom: 1px solid var(--color-mercury);
    }
  }

  &__title--2 {
    @include _1150 {
      border: none;
    }
  }

  &__title--3 {
    @include _1150 {
      position: absolute;
      top: 126px;
      left: 16px;
      right: 16px;
      border-bottom: 1px solid var(--color-mercury);
    }
  }

  &-icon {
    width: 15px;
    height: 15px;
    position: absolute;
    top: 2px;
    right: 4px;
    display: inline-block;
    background-image: url("../img/catalog-arrow.svg");
    background-repeat: no-repeat;
    background-position: right center;
    transition: transform .3s ease-in-out;

    @include _1150 {
      transform: rotate(-90deg);
    }
  }

  &-icon--mobile {
    position: absolute;
    left: 0;
    top: 4px;
    height: 20px;
    width: 16px;
    background-image: url('../img/left-arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
  }

  @include _1150 {
    &-icon--price {
    display: none;
  }
  }



  &-icon--main {
    left: 16px;
    top: 28px;

  }

  &__content {
    margin-left: -16px;
    padding-left: 16px;
    min-height: auto;
    height: auto;
    margin-bottom: 22px;
    border-bottom: 1px solid var(--color-mercury);
    transition: left .3s ease-in-out,
    right .3s ease-in-out,
    opacity .3s ease-in-out,
    visibility .3s ease-in-out;

    @include _1150 {
      border-bottom: none;
    }

    &--1 {
      padding-bottom: 22px;

      @include _1150 {
        position: absolute;
        top: 0;
        left: -100vw;
        opacity: 0;
        visibility: hidden;
        background-color: var(--color-white);
        z-index: 100;
      }
    }

    &--2 {
      padding-bottom: 28px;
    }

    &--3 {
      border-bottom: none;

      @include _1150 {
        position: absolute;
        top: 0;
        left: -100vw;
        opacity: 0;
        visibility: hidden;
        background-color: var(--color-white);
        z-index: 100;
      }
    }
  }

  &__content-inner {
    display: none;

    @include _1150 {
      padding-top: 24px;
      padding-bottom: 12px;
      margin-bottom: 12px;
      display: flex;
      align-items: start;
      justify-content: space-between;
      border-bottom: 1px solid var(--color-mercury);
    }
  }

  &__content--active {
    left: 16px;
    right: 16px;
    opacity: 1;
    visibility: visible;
    transition: left .3s ease-in-out,
    right .3s ease-in-out,
    opacity .3s ease-in-out,
    visibility .3s ease-in-out;
  }

  .ui-state-active &-icon {
    transform: rotate(180deg);
    transition: transform .3s ease-in-out;
  }

  &__search {
    display: flex;
    align-items: center;
    margin-bottom: 14px;
  }

  &__input {
    width: 100%;
    height: 40px;
    border-radius: var(--border-radius) 0 0 var(--border-radius);
    border: 1px solid var(--color-alto);
    border-right: none;
  }

  &__btn {
    height: 40px;
    width: 40px;
    border-radius: 0 var(--border-radius) var(--border-radius) 0;
    border: 1px solid var(--color-alto);
    border-left: none;
    background-image: url("../img/search-icon-2.svg");
    background-repeat: no-repeat;
    background-position: right 5px center;
  }

  &__subtitle {
    display: block;
    font-size: 14px;
    margin-bottom: 18px;
    color: var(--color-matisse);
  }
}

.ui-accordion-content-active {
  min-height: auto;
}

.catalog-price {

  &__wrapper {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 33px;

    &::before {
      @include ghost;
      width: 13px;
      height: 1px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: var(--color-tundora);
    }
  }

  &__input {
    width: 135px;
    height: 40px;
    background: #FFFFFF;
    border: 1px solid var(--color-alto);
    border-radius: var(--border-radius);
  }
}

.noUi-horizontal {
  height: 5px;
  border-radius: 8px;
  background-color: var(--color-sail);
}

.noUi-connect {
  height: 8px;
  background-color: var(--color-mariner);
}

.noUi-horizontal .noUi-handle {
  width: 18px;
  height: 18px;
}

.noUi-handle {
  cursor: pointer;
  border-radius: 100%;
  border: 3px solid var(--color-matisse);
  box-sizing: border-box;
  background-color: var(--color-white);
  box-shadow: 0px 12px 8px -6px rgba(174, 181, 239, 0.2);
}

.noUi-handle:after, .noUi-handle:before {
  display: none;
}

.noUi-target {
  border: none;
  box-shadow: none;
}

