.main__container {
    padding: 24px 16px 0;
    flex-grow: 0;
    display: flex;
    justify-content: space-between;

    @include _1150 {
      padding: 11px 5px 0;
    }

    @include _480 {
      padding-top: 17px;
    }

    .main-list {

      &__close-icon {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 0;
        right: 0;
        background-image: url("../img/close.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 25px;
      }

      &__title--mobile {
        display: none;
        position: relative;
        font-size: 22px;
        font-weight: 500;
        padding-bottom: 12px;
        border-bottom: 1px solid var(--color-mercury);
        color: var(--color-mine-shaft);

        @include _675 {
          display: block;
        }
      }
        align-self: flex-start;
        min-width: 320px;
        width: calc(30% + 30px);
        margin-right: 15px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 30px;
        background-color: var(--color-white);
        border-radius: var(--border-radius);
        box-shadow: -1px 4px 13px rgba(0, 0, 0, 0.05);

        @include _1150 {
          display: none;
        }

        @include _675 {
          padding-bottom: 0;
        }

        @include _480 {
          padding-left: 0;
          padding-right: 0;
          min-width: auto;
        }

        &__title {
          display: flex;
          margin: 0;
          padding: 13px 0 15px 21px;
          margin: 0 -10px 18px;
          font-size: 18px;
          font-weight: 400;
          box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
          border-radius: var(--border-radius) var(--border-radius) 0 0;
          background-color: var(--color-bright-sun);
          color: var(--color-tundora);

          svg  {
            display: block;
            margin-right: 15px;
            width: 23px;
            height: 21px;
          }

          @include _1150 {
            justify-content: center;
            padding-left: 0;
            margin-bottom: 20px;
            background-position: 278px center;
          }

          @include _675 {
            border-radius: var(--border-radius);
          }

          @include _480 {
            margin: 0 0 8px;
          }
        }

        &__item:not(:last-child) {
            margin-bottom: 16px;

            @include _1150 {
              margin-bottom: 24px;
            }
        }

        &__link {
            display: block;
            position: relative;
            max-width: 325px;
            font-size: 16px;
            line-height: 18px;
            transition: color .3s ease-out,
            text-decoration .3s ease-in-out;
            color: var(--color-matisse);

            &:hover {
                transition: color .3s ease-out,
                 text-decoration .3s ease-in-out;
                color: var(--color-sunset-orange);
                text-decoration: underline;
            }

            @include _1150 {
              line-height: 27px;
              font-size: 18px;
            }

            @include _675 {
              max-width: 100%;
            }
        }

        &__wrapper {
          @include _1150 {
            display: flex;
            padding: 0 15px;
          }

          @include _675 {
            display: block;
          }
        }

        &__list {
          @include _1150 {
            width: 44%;

            &:first-child {
              margin-right: 40px;
              @include _675 {
                margin-right: 0;
              }
            }

            &:last-child {
              padding-left: 28px;
              border-left: 2px solid var(--color-mercury);

              @include _675 {
                padding-left: 0;
              border-left: none;
              }
            }
          }

          @include _755 {
            width: auto;

            &:first-child {
              margin-right: 5px;
            }

            &:last-child {
              padding-left: 10px;
              border-left: 2px solid var(--color-mercury);

              @include _675 {
                padding-left: 0;
              border-left: none;
              }
            }
          }
        }
    }

    .main-list--tablet {
      display: none;
      position: relative;

      @include _1150 {
        display: block;
        width: 100%;
        background-color: var(--color-white);
        box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
      }

      &__wrapper {
        @include _1150 {
          display: flex;
          padding: 0 15px;
        }
      }
    }

    .main-list__wrapper--tablet {
      @include _675 {
        position: absolute;
        opacity: 0;
        visibility: hidden;
        top: 0;
        left: 50%;
        z-index: 300;
        transform: translateX(-50%);
        background-color: var(--color-white);
        transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
      }

      &.main-list--visible {
        opacity: 1;
        visibility: visible;
        transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
      }
    }

    .main-content {
      max-width: 1076px;
      overflow: auto;

      @include _1150 {
        padding: 0 10px;
        max-width: 1170px;
      }

      @include _480 {
        padding: 0 3px;
      }
    }

    &--catalog {
      padding-top: 12px;
    }

    .catalog-list {
      position: absolute;
      top: 61px;
      left: 15px;
      width: auto;
      z-index: 100;
      margin-right: 0;
      padding-top: 18px;
      border-radius: 0 0 var(--border-radius) var(--border-radius);
      opacity: 0;
      visibility: hidden;
      transition:
      opacity .3s ease-in-out,
      visibility .3s ease-in-out;

       &__btn {
         position: relative;
         min-width: 347px;
         margin: 0;
         z-index: 150;
         margin-bottom: 24px;
         padding-left: 12px;
         border-radius: var(--border-radius);

         @include _1150 {
           min-width: 100%;
         }

         &::before {
           content: '';
           position: absolute;
           width: 15px;
           height: 15px;
           right: 15px;
           top: 16px;
           transition: transform .3s ease-in-out;
           background-image: url("../img/catalog-arrow.svg");
           background-repeat: no-repeat;
           background-position: center;

           @include _1150 {
             display: none;
           }
         }

         &--active.catalog-list__btn::before {
         transform: rotate(180deg);
         transition: transform .3s ease-in-out;
       }
       }

       &--visible {
         visibility: visible;
         opacity: 1;
         transition:
         opacity .3s ease-in-out,
         visibility .3s ease-in-out;
       }
    }

}
