.breadcrumbs {
  display: flex;
  align-items: center;
  margin-bottom: 28px;

  &__tohome {
    display: inline-block;
    margin-right: 14px;

    svg {
      display: block;
      width: 16px;
      height: 16px;
    }
  }

  &__item {
    background-image: url("../img/breadcrumbs-arrow.svg");
    background-repeat: no-repeat;
    background-position: left center;
    padding-left: 18px;
  }

  &__link {
    font-size: 14px;
    font-weight: 500;
    color: var(--color-tundora);
  }
}
